#about {
  background-color: #def2f1;
  color: #3aafa9;
  height: 100%;
  border-top: 0;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%);
  padding-bottom: 25%;
  width: 100vw;
}

i {
  font-size: 3em;
  margin-left: 0.5em;
  color: #3aafa9 !important;
}

i:hover {
  color: #2b7a78 !important;
  background-color: #def2f1 !important;
}

#goToProjects {
  width: 11em;
  margin-top: 1.5em;
  color: #3aafa9 !important;
  border: 1px solid #3aafa9 !important;
}
.profilePic {
  /* min-width: auto; */
  max-width: 85%;
  max-height: 85%;
}
@media only screen and (max-width: 600px) {
  .profilePic {
    max-width: 100%;
    max-height: 60%;
  }
}

@media only screen and (max-width: 600px) {
  #about {
    padding-bottom: 55%;
  }
}

#goToProjects:hover {
  color: #def2f1 !important;
  background-color: #2b7a78 !important;
}

.container-fluid h2 {
  font-size: 4em;
}

.container-fluid p {
  font-size: 1.1em;
}
