button {
  width: 7.5em;
}

.image {
  display: block;
  width: 100%;
  min-height: 100%;
  border-radius: 1em;
}

.text {
  color: #def2f1;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #314455;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.75s ease;
  border-radius: 1em;
}

.imgContainer:hover .overlay {
  height: 100%;
}

.text h4 {
  font-size: 2em;
}

.description h3 {
  font-size: 2.5em;
}

#project-description {
  font-size: 1.15em;
}

.imgContainer {
  -webkit-filter: drop-shadow(5px 2px 4px #314455);
          filter: drop-shadow(5px 2px 4px #314455);
}

#about {
  background-color: #def2f1;
  color: #3aafa9;
  height: 100%;
  border-top: 0;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%);
  padding-bottom: 25%;
  width: 100vw;
}

i {
  font-size: 3em;
  margin-left: 0.5em;
  color: #3aafa9 !important;
}

i:hover {
  color: #2b7a78 !important;
  background-color: #def2f1 !important;
}

#goToProjects {
  width: 11em;
  margin-top: 1.5em;
  color: #3aafa9 !important;
  border: 1px solid #3aafa9 !important;
}
.profilePic {
  /* min-width: auto; */
  max-width: 85%;
  max-height: 85%;
}
@media only screen and (max-width: 600px) {
  .profilePic {
    max-width: 100%;
    max-height: 60%;
  }
}

@media only screen and (max-width: 600px) {
  #about {
    padding-bottom: 55%;
  }
}

#goToProjects:hover {
  color: #def2f1 !important;
  background-color: #2b7a78 !important;
}

.container-fluid h2 {
  font-size: 4em;
}

.container-fluid p {
  font-size: 1.1em;
}

#projectContainer {
  background-color: #02aab0;
  background-image: linear-gradient(135deg, #3aafa9, #2b7a78);
  color: #314455;
  height: 100%;
  border-top: 0;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 92%, 0 100%);
  clip-path: polygon(0 0, 100% 2.5%, 100% 97%, 0 100%);
  padding-top: 22.5%;
  padding-bottom: 15%;
  width: 100vw;
  margin-top: 2em;
}

.btn {
  color: #314455 !important;
  border: 1px solid #314455 !important;
}
.btn:hover {
  color: #def2f1 !important;
  background-color: #314455 !important;
}

@media only screen and (max-width: 600px) {
  #projectContainer {
    padding-top: 55%;
  }
}

